import Layout from './Layout'
import { createSelector } from 'reselect'
import { connect } from 'react-redux'

import { loginUser } from '../redux/actions/user-actions'

class Login extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      email: null,
      password: null,
      formType: 'login'
    }
  }

  onHandleInputChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  onHandleLogin = (event) => {
    event.preventDefault()
    this.props.onLoginUser(this.state.email, this.state.password);
  }

  changeFormToSignUp = () => {
    this.setState({
      formType: 'signup'
    })
  }

  changeFormToLogin = () => {
    this.setState({
      formType: 'login'
    })
  }

  test = () => {
    console.log(this.props.user)
  }

  render() {
    return(
      <div>
        {this.state.formType === 'login' &&
          <form onSubmit={this.onHandleLogin}>
            <input onChange={this.onHandleInputChange} name="email" type="email"/>
            <input onChange={this.onHandleInputChange} name="password" type="password"/>
            <button type="submit">Login</button>
            <button onClick={this.changeFormToSignUp}>Signup</button>
          </form>
        }
        {this.state.formType === 'signup' &&
          <form onSubmit={this.onHandleLogin}>
            <input onChange={this.onHandleInputChange} name="email" type="email"/>
            <input onChange={this.onHandleInputChange} name="password" type="password"/>
            <button type="submit">Signup</button>
          </form>
        }
        <button onClick={this.test}>test</button>
      </div>
    )
  }
}

const userSelector = createSelector(
  state => state.user,
  user => user
)

const mapStateToProps = createSelector(
  userSelector,
  (user) => ({
    user
  })
);

const mapActionsToProps = {
  onLoginUser: loginUser
}

export default Layout(connect(mapStateToProps, mapActionsToProps)(Login))